import React, {useEffect, useState} from 'react';
import Navbar from "../components/NavBar";
import {connect} from 'react-redux';
import { checkAuthenticated} from '../actions/auth';
import NotificationBox from '../components/NotificationBox';

const Layout = ({ children, checkAuthenticated }) => {
    const [notificationBox, setNotificationBox] = useState(true);

    useEffect(() => {
        checkAuthenticated();
    }, []);

    const toggleNotificationBox = () => {
        setNotificationBox(!notificationBox)
    }
    
    return (
        <>
            <Navbar toggleNotificationBox={toggleNotificationBox}/>
            <header className="App-header">
            {notificationBox && <NotificationBox/>}
            {children}
            </header>
        </>
    )
}

export default connect(null, {checkAuthenticated})(Layout)